.intro_body {
    padding-top: 15vh;
    padding-left: 10vw;
    padding-right: 10vw;
    padding-bottom: 15vh;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 8888; 
}

.popup {
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  z-index: 10000;
}
